<template>
  <div class="web-config-view">
    <div class="page-header">
      <h1>Web </h1>
      <div class="header-actions">
        <el-button @click="handleReset"></el-button>
        <el-button
          type="primary"
          :loading="saving"
          @click="handleSave"
        >
          
        </el-button>
      </div>
    </div>

    <el-alert
      type="warning"
      :closable="false"
      show-icon
      style="margin-bottom: 24px;"
    >
      <template #title>
         Web 
      </template>
    </el-alert>

    <el-card>
      <el-form
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="140px"
        label-position="left"
      >
        <el-form-item label="" prop="title">
          <el-input
            v-model="formData.title"
            placeholder="MioChat"
            maxlength="50"
            show-word-limit
            style="width: 400px"
          />
          <template #extra>
            <span class="form-item-tip">
              
            </span>
          </template>
        </el-form-item>

        <el-form-item label="" prop="beian">
          <el-input
            v-model="formData.beian"
            placeholder=""
            maxlength="100"
            show-word-limit
            style="width: 400px"
          />
          <template #extra>
            <span class="form-item-tip">
              ICP12345678
            </span>
          </template>
        </el-form-item>

        <el-divider />

        <el-form-item label="" prop="admin_code">
          <el-input
            v-model="formData.admin_code"
            :type="showAdminCode ? 'text' : 'password'"
            placeholder=""
            maxlength="50"
            show-word-limit
            style="width: 400px"
          >
            <template #append>
              <el-button
                :icon="showAdminCode ? View : Hide"
                @click="showAdminCode = !showAdminCode"
              />
            </template>
          </el-input>
          <template #extra>
            <span class="form-item-tip">
              
            </span>
          </template>
        </el-form-item>

        <el-form-item label="" prop="user_code">
          <el-input
            v-model="formData.user_code"
            :type="showUserCode ? 'text' : 'password'"
            placeholder=""
            maxlength="50"
            show-word-limit
            style="width: 400px"
          >
            <template #append>
              <el-button
                :icon="showUserCode ? View : Hide"
                @click="showUserCode = !showUserCode"
              />
            </template>
          </el-input>
          <template #extra>
            <span class="form-item-tip">
              
            </span>
          </template>
        </el-form-item>

        <el-divider />

        <el-form-item label="" prop="full_screen">
          <el-switch
            v-model="formData.full_screen"
            active-text=""
            inactive-text=""
          />
          <template #extra>
            <span class="form-item-tip">
              
            </span>
          </template>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { View, Hide } from '@element-plus/icons-vue';
import { useConfigStore } from '@/stores/configStore.js';

const configStore = useConfigStore();
const formRef = ref(null);
const saving = ref(false);
const showAdminCode = ref(false);
const showUserCode = ref(false);

// 
const formData = reactive({
  title: 'MioChat',
  beian: '',
  admin_code: '',
  user_code: '',
  full_screen: true
});

// 
const originalData = reactive({});

// 
const rules = {
  title: [
    { required: true, message: '', trigger: 'blur' }
  ],
  admin_code: [
    { required: true, message: '', trigger: 'blur' },
    { min: 6, message: ' 6 ', trigger: 'blur' }
  ]
};

// 
const loadConfig = async () => {
  try {
    const webConfig = configStore.config?.web || await configStore.fetchConfigSection('web');
    
    Object.assign(formData, {
      title: webConfig.title || 'MioChat',
      beian: webConfig.beian || '',
      admin_code: webConfig.admin_code || '',
      user_code: webConfig.user_code || '',
      full_screen: webConfig.full_screen ?? true
    });
    
    // 
    Object.assign(originalData, formData);
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const handleSave = async () => {
  try {
    await formRef.value.validate();
    
    await ElMessageBox.confirm(
      ' Web ',
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    );

    saving.value = true;

    await configStore.updateConfigSection('web', {
      title: formData.title,
      beian: formData.beian,
      admin_code: formData.admin_code,
      user_code: formData.user_code,
      full_screen: formData.full_screen
    });

    ElMessage.success('');
    
    // 
    Object.assign(originalData, formData);
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  } finally {
    saving.value = false;
  }
};

// 
const handleReset = () => {
  Object.assign(formData, originalData);
  formRef.value?.clearValidate();
  ElMessage.info('');
};

// 
onMounted(async () => {
  if (!configStore.config) {
    await configStore.fetchConfig();
  }
  await loadConfig();
});
</script>

