<template>
  <div class="llm-adapters-view">
    <!--  -->
    <div v-if="loading" class="loading-container">
      <div class="skeleton-header">
        <div class="skeleton-title"></div>
        <div class="skeleton-actions">
          <div class="skeleton-button" v-for="i in 3" :key="i"></div>
        </div>
      </div>
      <div class="skeleton-table">
        <div class="skeleton-row" v-for="i in 6" :key="i">
          <div class="skeleton-cell" v-for="j in 6" :key="j"></div>
        </div>
      </div>
    </div>

    <transition name="fade" mode="out-in">
      <div v-if="!loading" class="content-container">
        <!--  -->
        <div class="page-header" :class="{ 'mobile': isMobile }">
          <h1 v-if="!isMobile">LLM </h1>
          
          <!--  -->
          <div v-if="!isMobile" class="header-actions">
            <el-button
              v-if="configStore.selectedAdapters && configStore.selectedAdapters.length > 0"
              type="success"
              :icon="Check"
              @click="handleBatchEnable"
            >
               ({{ configStore.selectedAdapters.length }})
            </el-button>
            <el-button
              v-if="configStore.selectedAdapters && configStore.selectedAdapters.length > 0"
              type="warning"
              :icon="Close"
              @click="handleBatchDisable"
            >
              
            </el-button>
            <el-button
              v-if="configStore.selectedAdapters && configStore.selectedAdapters.length > 0"
              type="danger"
              :icon="Delete"
              @click="handleBatchDelete"
            >
              
            </el-button>
            <el-button
              type="primary"
              :icon="Refresh"
              :loading="refreshing"
              @click="handleRefreshAll"
            >
              
            </el-button>
            <el-dropdown @command="handleAddAdapter" trigger="click">
              <el-button type="primary" :icon="Plus">
                 <el-icon class="el-icon--right"><ArrowDown /></el-icon>
              </el-button>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item
                    v-for="type in adapterTypes"
                    :key="type"
                    :command="type"
                  >
                    <el-icon><Connection /></el-icon>
                    {{ formatTypeLabel(type) }}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </div>

          <!--  -->
          <div v-if="isMobile" class="mobile-actions">
            <el-button
              type="primary"
              :icon="Refresh"
              :loading="refreshing"
              @click="handleRefreshAll"
              size="small"
            >
              
            </el-button>
            <el-dropdown @command="handleAddAdapter" trigger="click">
              <el-button type="primary" :icon="Plus" size="small">
                
              </el-button>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item
                    v-for="type in adapterTypes"
                    :key="type"
                    :command="type"
                  >
                    <el-icon><Connection /></el-icon>
                    {{ formatTypeLabel(type) }}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </div>
        </div>

        <!--  -->
        <div v-if="isMobile && configStore.selectedAdapters && configStore.selectedAdapters.length > 0" class="mobile-batch-actions">
          <div class="batch-info">
             {{ configStore.selectedAdapters.length }} 
          </div>
          <div class="batch-buttons">
            <el-button type="success" size="small" @click="handleBatchEnable"></el-button>
            <el-button type="warning" size="small" @click="handleBatchDisable"></el-button>
            <el-button type="danger" size="small" @click="handleBatchDelete"></el-button>
          </div>
        </div>

        <!--  -->
        <div class="stats-bar">
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ allAdapters.length }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ enabledCount }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ configStore.totalModelsCount }} </span>
          </div>
        </div>

        <!--  -->
        <div class="adapters-list">
          <div v-if="allAdapters.length === 0" class="empty-state">
            <el-empty description="">
              <el-dropdown @command="handleAddAdapter" trigger="click">
                <el-button type="primary" :icon="Plus">
                  
                </el-button>
                <template #dropdown>
                  <el-dropdown-menu>
                    <el-dropdown-item
                      v-for="type in adapterTypes"
                      :key="type"
                      :command="type"
                    >
                      <el-icon><Connection /></el-icon>
                      {{ formatTypeLabel(type) }}
                    </el-dropdown-item>
                  </el-dropdown-menu>
                </template>
              </el-dropdown>
            </el-empty>
          </div>

          <el-table
            v-else
            :data="allAdapters"
            style="width: 100%"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="55" />
            
            <el-table-column label="" width="120">
              <template #default="{ row }">
                <el-tag :type="getTypeTagType(row.type)" effect="light">
                  {{ formatTypeLabel(row.type) }}
                </el-tag>
              </template>
            </el-table-column>

            <el-table-column label="" min-width="150">
              <template #default="{ row }">
                <div class="adapter-name">
                  <span class="name">{{ row.adapter.name || `${row.type}-${row.index + 1}` }}</span>
                  <el-tag v-if="!row.adapter.enable" type="info" size="small" effect="plain">
                    
                  </el-tag>
                </div>
              </template>
            </el-table-column>

            <el-table-column label="" min-width="200">
              <template #default="{ row }">
                <span class="model-name">{{ row.adapter.default_model || '-' }}</span>
              </template>
            </el-table-column>

            <el-table-column label="" width="120" align="center">
              <template #default="{ row }">
                <el-tag size="small" :type="getModelCountType(row.modelCount)" effect="plain" round>
                  {{ row.modelCount }} 
                </el-tag>
              </template>
            </el-table-column>

            <el-table-column label="" width="100" align="center">
              <template #default="{ row }">
                <el-switch
                  v-model="row.adapter.enable"
                  @change="handleToggle(row)"
                />
              </template>
            </el-table-column>

            <el-table-column label="" width="200" align="center">
              <template #default="{ row }">
                <div class="action-buttons">
                  <el-button
                    size="small"
                    type="primary"
                    text
                    @click="handleEdit(row)"
                  >
                    
                  </el-button>
                  <el-button
                    size="small"
                    type="success"
                    text
                    @click="handleRefresh(row)"
                  >
                    
                  </el-button>
                  <el-button
                    size="small"
                    type="danger"
                    text
                    @click="handleDelete(row)"
                  >
                    
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </transition>

    <!--  -->
    <adapter-editor
      :visible="editorVisible"
      :mode="editorMode"
      :type="editorType"
      :adapter="editorAdapter"
      :index="editorIndex"
      @close="handleEditorClose"
      @submit="handleEditorSubmit"
    />
  </div>
</template>

<script setup>
import AdapterEditor from '@/components/settings/AdapterEditor.vue';
import { useConfigStore } from '@/stores/configStore.js';
import {
  ArrowDown,
  Check,
  Close,
  Connection,
  Delete,
  Plus,
  Refresh
} from '@element-plus/icons-vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { computed, onMounted, onUnmounted, ref } from 'vue';

const configStore = useConfigStore();

const loading = ref(true);
const refreshing = ref(false);
const editorVisible = ref(false);
const editorMode = ref('add');
const editorType = ref('openai');
const editorAdapter = ref(null);

// 
const isMobile = ref(false);

const checkMobile = () => {
  isMobile.value = window.innerWidth <= 768;
};
const editorIndex = ref(-1);

// 
const adapterTypes = computed(() => {
  return Object.keys(configStore.adapters);
});

// 
const allAdapters = computed(() => {
  const adapters = [];
  Object.entries(configStore.adapters).forEach(([type, instances]) => {
    instances.forEach((adapter, index) => {
      const providerName = adapter.name || `${type}-${index + 1}`;
      const models = configStore.models[providerName] || [];
      const modelCount = models.reduce((count, group) => {
        return count + (group.models ? group.models.length : 0);
      }, 0);
      
      adapters.push({
        type,
        index,
        adapter,
        providerName,
        models,
        modelCount
      });
    });
  });
  return adapters;
});

// 
const enabledCount = computed(() => {
  return allAdapters.value.filter(item => item.adapter.enable).length;
});

// 
const getAdapterModels = (adapter, type, index) => {
  const providerName = adapter.name || `${type}-${index + 1}`;
  return configStore.models[providerName] || [];
};

// 
const formatTypeLabel = (type) => {
  const map = {
    openai: 'OpenAI',
    gemini: 'Gemini',
    vertex: 'Vertex AI'
  };
  if (map[type]) return map[type];
  return type.charAt(0).toUpperCase() + type.slice(1);
};

// 
const getTypeTagType = (type) => {
  const map = {
    openai: 'success',
    gemini: 'warning',
    vertex: 'info'
  };
  return map[type] || 'primary';
};

// 
const getModelCountType = (count) => {
  if (count === 0) return 'danger';
  if (count < 5) return 'warning';
  return 'success';
};

// 
const handleAddAdapter = (type) => {
  editorMode.value = 'add';
  editorType.value = type;
  editorAdapter.value = null;
  editorIndex.value = -1;
  editorVisible.value = true;
};

// 
const handleEdit = (row) => {
  const { type, index, adapter } = row;
  editorMode.value = 'edit';
  editorType.value = type;
  editorAdapter.value = adapter;
  editorIndex.value = index;
  editorVisible.value = true;
};

// 
const handleDelete = async (row) => {
  const { type, index, adapter } = row;
  const adapterName = adapter.name || `${type}-${index + 1}`;
  
  try {
    await ElMessageBox.confirm(
      ` "${adapterName}" `,
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    );

    await configStore.deleteAdapter(type, index);
    ElMessage.success('');
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  }
};

// 
const handleRefresh = async (row) => {
  const { type, index } = row;
  try {
    await configStore.refreshAdapterModels(type, index);
    ElMessage.success('');
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// /
const handleToggle = async (row) => {
  const { type, index, adapter } = row;
  const enable = adapter.enable;
  
  try {
    //  enable 
    const updatedData = { ...adapter, enable };
    await configStore.updateAdapter(type, index, updatedData);
    ElMessage.success(enable ? '' : '');
  } catch (error) {
    // 
    adapter.enable = !enable;
    ElMessage.error('' + error.message);
  }
};

// 
const handleSelectionChange = (selection) => {
  // 
  configStore.clearAdapterSelection();
  
  // 
  selection.forEach(item => {
    configStore.toggleAdapterSelection(item.type, item.index);
  });
};

// 
const handleRefreshAll = async () => {
  refreshing.value = true;
  try {
    await configStore.refreshAllModels();
    ElMessage.success('');
  } catch (error) {
    ElMessage.error('' + error.message);
  } finally {
    refreshing.value = false;
  }
};

// 
const handleBatchEnable = async () => {
  if (!configStore.selectedAdapters || configStore.selectedAdapters.length === 0) {
    ElMessage.warning('');
    return;
  }
  
  try {
    const results = await configStore.batchToggleAdapters(
      configStore.selectedAdapters,
      true
    );
    
    const successCount = results.filter(r => r.success).length;
    ElMessage.success(` ${successCount} `);
    configStore.clearAdapterSelection();
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const handleBatchDisable = async () => {
  if (!configStore.selectedAdapters || configStore.selectedAdapters.length === 0) {
    ElMessage.warning('');
    return;
  }
  
  try {
    const results = await configStore.batchToggleAdapters(
      configStore.selectedAdapters,
      false
    );
    
    const successCount = results.filter(r => r.success).length;
    ElMessage.success(` ${successCount} `);
    configStore.clearAdapterSelection();
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const handleBatchDelete = async () => {
  if (!configStore.selectedAdapters || configStore.selectedAdapters.length === 0) {
    ElMessage.warning('');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      ` ${configStore.selectedAdapters.length} `,
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    );

    const results = await configStore.batchDeleteAdapters(
      configStore.selectedAdapters
    );
    
    const successCount = results.filter(r => r.success).length;
    ElMessage.success(` ${successCount} `);
    configStore.clearAdapterSelection();
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  }
};

// 
const handleEditorClose = () => {
  editorVisible.value = false;
};

// 
const handleEditorSubmit = async ({ type, index, data, mode }) => {
  try {
    if (mode === 'add') {
      await configStore.addAdapter(type, data);
      ElMessage.success('');
    } else {
      await configStore.updateAdapter(type, index, data);
      ElMessage.success('');
    }
  } catch (error) {
    ElMessage.error(mode === 'add' ? '' : '' + error.message);
    throw error; // 
  }
};

// 
onMounted(async () => {
  // 
  checkMobile();
  window.addEventListener('resize', checkMobile);
  
  loading.value = true;
  try {
    if (!configStore.config) {
      await configStore.fetchConfig();
    }
    await new Promise(resolve => setTimeout(resolve, 300));
  } catch (error) {
    ElMessage.error('' + error.message);
  } finally {
    loading.value = false;
  }
});

onUnmounted(() => {
  window.removeEventListener('resize', checkMobile);
});
</script>

