              </el-button>
            </div>
          </el-card>
        </div>
      </div>
    </transition>

    <!--  -->
    <el-dialog
      v-model="detailDialogVisible"
      :title="`${currentPlugin?.displayName} - `"
      width="800px"
      destroy-on-close
      class="config-dialog plugin-detail-dialog"
    >
      <div class="dialog-content">
        <div v-if="currentPlugin" class="plugin-detail">
        <el-descriptions :column="2" border>
          <el-descriptions-item label="">
            {{ currentPlugin.displayName }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ currentPlugin.version }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ currentPlugin.author }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            <el-tag :type="currentPlugin.type === 'standard' ? 'success' : 'warning'">
              {{ currentPlugin.type === 'standard' ? '' : '' }}
            </el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="">
            <el-tag :type="currentPlugin.enabled ? 'success' : 'info'">
              {{ currentPlugin.enabled ? '' : '' }}
            </el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ currentPlugin.toolCount }}
          </el-descriptions-item>
          <el-descriptions-item label="" :span="2">
            {{ currentPlugin.description || '' }}
          </el-descriptions-item>
        </el-descriptions>

        <!--  -->
        <div v-if="pluginTools.length > 0" class="tools-section">
          <h4></h4>
          <el-collapse v-model="activeCollapseNames">
            <el-collapse-item
              v-for="group in pluginTools"
              :key="group.group"
              :name="group.group"
              :title="`${group.group} (${group.tools.length})`"
            >
              <div
                v-for="tool in group.tools"
                :key="tool.name"
                class="tool-item"
              >
                <div class="tool-header">
                  <h5>{{ tool.name }}</h5>
                  <el-button
                    size="small"
                    type="primary"
                    :icon="Cpu"
                    @click="handleDebugTool(tool)"
                  >
                    
                  </el-button>
                </div>
                <p class="tool-description">{{ tool.description }}</p>
                <el-tag size="small" type="info">
                  : {{ Object.keys(tool.parameters?.properties || {}).length }}
                </el-tag>
              </div>
            </el-collapse-item>
          </el-collapse>
        </div>
        </div>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      v-model="configDialogVisible"
      :title="`${currentPlugin?.displayName} - `"
      width="700px"
      destroy-on-close
      class="config-dialog plugin-config-dialog"
    >
      <div class="dialog-content">
        <div class="config-editor">
          <el-alert
            type="info"
            :closable="false"
            show-icon
            style="margin-bottom: 16px;"
          >
            <template #title>
              
            </template>
          </el-alert>
          
          <el-input
            v-model="configJsonStr"
            type="textarea"
            :rows="15"
            placeholder="JSON "
            class="config-textarea"
          />
        </div>
      </div>
      
      <template #footer>
        <el-button @click="configDialogVisible = false"></el-button>
        <el-button
          type="primary"
          :loading="savingConfig"
          @click="handleSaveConfig"
        >
          
        </el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      v-model="debugDialogVisible"
      :title="`: ${currentTool?.name}`"
      width="800px"
      destroy-on-close
      class="debug-dialog"
    >
      <div class="debug-container" style="height: 60vh; overflow-y: auto;">
        <!--  -->
        <el-card class="tool-info-card" shadow="never">
          <div class="tool-info">
            <div class="info-row">
              <label></label>
              <el-tag type="primary">{{ currentTool?.name }}</el-tag>
            </div>
            <div class="info-row">
              <label></label>
              <span>{{ currentTool?.description || '' }}</span>
            </div>
          </div>
        </el-card>

        <!--  -->
        <el-card class="params-schema-card" shadow="never" v-if="currentTool?.parameters">
          <template #header>
            <div class="card-header">
              <span></span>
              <el-button
                size="small"
                type="primary"
                text
                @click="fillExampleParams"
              >
                
              </el-button>
            </div>
          </template>
          
          <div class="params-schema">
            <div
              v-for="(prop, key) in currentTool?.parameters?.properties"
              :key="key"
              class="param-item"
            >
              <div class="param-header">
                <span class="param-name">
                  {{ key }}
                  <el-tag
                    v-if="currentTool?.parameters?.required?.includes(key)"
                    type="danger"
                    size="small"
                    effect="plain"
                  >
                    
                  </el-tag>
                  <el-tag
                    v-else
                    type="info"
                    size="small"
                    effect="plain"
                  >
                    
                  </el-tag>
                </span>
                <el-tag size="small" type="info">{{ prop.type }}</el-tag>
              </div>
              <div class="param-description">{{ prop.description || '' }}</div>
              <div v-if="prop.enum" class="param-enum">
                : {{ prop.enum.join(', ') }}
              </div>
              <div v-if="prop.items" class="param-items">
                : {{ prop.items.type }}
              </div>
            </div>
          </div>
        </el-card>

        <!--  -->
        <el-card class="params-input-card" shadow="never">
          <template #header>
            <div class="card-header">
              <span> (JSON )</span>
              <el-button
                size="small"
                type="warning"
                text
                @click="debugParams = '{}'"
              >
                
              </el-button>
            </div>
          </template>
          
          <el-input
            v-model="debugParams"
            type="textarea"
            :rows="6"
            placeholder=' JSON : {"query": "test", "limit": 10}'
            class="params-textarea"
          />
          
          <div class="params-hint">
            <el-icon><InfoFilled /></el-icon>
            :  JSON 
          </div>
        </el-card>

        <!--  -->
        <div v-if="debugResult" class="result-container">
          <el-card class="result-card" shadow="never">
            <template #header>
              <div class="card-header">
                <span></span>
              </div>
            </template>
            
            <div class="debug-result-wrapper" :class="debugResult.success ? 'status-success' : 'status-error'">
              <div class="result-header">
                <div class="status-indicator">
                  <el-icon :size="18" class="icon">
                    <CircleCheckFilled v-if="debugResult.success" />
                    <CircleCloseFilled v-else />
                  </el-icon>
                  <span class="status-text">{{ debugResult.success ? '' : '' }}</span>
                </div>
                <div v-if="debugResult.success" class="execution-meta">
                  <span class="meta-item">: {{ debugResult.executionTime }}</span>
                </div>
              </div>

              <div class="result-body">
                <template v-if="debugResult.success">
                  <div class="plugin-info-bar">
                    <span class="info-item">
                      <span class="label">:</span>
                      <span class="value">{{ debugResult.pluginName }}</span>
                    </span>
                    <span class="divider">|</span>
                    <span class="info-item">
                      <span class="label">:</span>
                      <span class="value">{{ debugResult.toolName }}</span>
                    </span>
                  </div>
                  
                  <div class="json-viewer-container">
                    <div class="viewer-label"></div>
                    <div class="json-content">
                      <pre>{{ JSON.stringify(debugResult.result, null, 2) }}</pre>
                    </div>
                  </div>
                </template>

                <template v-else>
                  <div class="error-content">
                    <div class="error-message-box">
                      <el-icon class="error-icon"><InfoFilled /></el-icon>
                      <span class="message-text">{{ debugResult.error?.message || '' }}</span>
                    </div>
                    <div v-if="debugResult.error?.stack" class="error-stack">
                      <div class="stack-label"></div>
                      <pre class="stack-content">{{ debugResult.error.stack }}</pre>
