<template>
  <div class="server-config-view">
    <div class="page-header">
      <h1></h1>
      <div class="header-actions">
        <el-button @click="handleReset"></el-button>
        <el-button
          type="primary"
          :loading="saving"
          @click="handleSave"
        >
          
        </el-button>
      </div>
    </div>

    <el-alert
      type="warning"
      :closable="false"
      show-icon
      style="margin-bottom: 24px;"
    >
      <template #title>
        
      </template>
    </el-alert>

    <el-card>
      <el-form
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="140px"
        label-position="left"
      >
        <el-form-item label="" prop="port">
          <el-input-number
            v-model="formData.port"
            :min="1"
            :max="65535"
            :step="1"
            controls-position="right"
            style="width: 200px"
          />
          <template #extra>
            <span class="form-item-tip">
              1-65535 3080
            </span>
          </template>
        </el-form-item>

        <el-form-item label="" prop="host">
          <el-input
            v-model="formData.host"
            placeholder="0.0.0.0"
            style="width: 300px"
          />
          <template #extra>
            <span class="form-item-tip">
              0.0.0.0 
            </span>
          </template>
        </el-form-item>

        <el-form-item label="" prop="max_rate_pre_min">
          <el-input-number
            v-model="formData.max_rate_pre_min"
            :min="1"
            :max="10000"
            :step="10"
            controls-position="right"
            style="width: 200px"
          />
          <span style="margin-left: 12px; color: #909399;">/</span>
          <template #extra>
            <span class="form-item-tip">
              
            </span>
          </template>
        </el-form-item>

        <el-divider />

        <el-form-item label="" prop="debug">
          <el-switch
            v-model="formData.debug"
            active-text=""
            inactive-text=""
          />
          <template #extra>
            <span class="form-item-tip">
              
            </span>
          </template>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { useConfigStore } from '@/stores/configStore.js';

const configStore = useConfigStore();
const formRef = ref(null);
const saving = ref(false);

// 
const formData = reactive({
  port: 3080,
  host: '0.0.0.0',
  max_rate_pre_min: 100,
  debug: false
});

// 
const originalData = reactive({});

// 
const rules = {
  port: [
    { required: true, message: '', trigger: 'blur' },
    {
      type: 'number',
      min: 1,
      max: 65535,
      message: ' 1-65535 ',
      trigger: 'blur'
    }
  ],
  host: [
    { required: true, message: '', trigger: 'blur' }
  ],
  max_rate_pre_min: [
    { required: true, message: '', trigger: 'blur' },
    {
      type: 'number',
      min: 1,
      message: ' 0',
      trigger: 'blur'
    }
  ]
};

// 
const loadConfig = async () => {
  try {
    const serverConfig = configStore.config?.server || await configStore.fetchConfigSection('server');
    const debugConfig = configStore.config?.debug ?? false;
    
    Object.assign(formData, {
      port: serverConfig.port || 3080,
      host: serverConfig.host || '0.0.0.0',
      max_rate_pre_min: serverConfig.max_rate_pre_min || 100,
      debug: debugConfig
    });
    
    // 
    Object.assign(originalData, formData);
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const handleSave = async () => {
  try {
    await formRef.value.validate();
    
    await ElMessageBox.confirm(
      '',
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    );

    saving.value = true;

    //  server 
    await configStore.updateConfigSection('server', {
      port: formData.port,
      host: formData.host,
      max_rate_pre_min: formData.max_rate_pre_min
    });

    //  debug 
    if (configStore.config) {
      configStore.config.debug = formData.debug;
    }

    ElMessage.success('');
    
    // 
    Object.assign(originalData, formData);
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  } finally {
    saving.value = false;
  }
};

// 
const handleReset = () => {
  Object.assign(formData, originalData);
  formRef.value?.clearValidate();
  ElMessage.info('');
};

// 
onMounted(async () => {
  if (!configStore.config) {
    await configStore.fetchConfig();
  }
  await loadConfig();
});
</script>

