<template>
  <div class="presets-view">
    <!--  -->
    <div v-if="loading" class="loading-container">
      <div class="skeleton-header">
        <div class="skeleton-title"></div>
        <div class="skeleton-actions">
          <div class="skeleton-button" v-for="i in 3" :key="i"></div>
        </div>
      </div>
      <div class="skeleton-table">
        <div class="skeleton-row" v-for="i in 6" :key="i">
          <div class="skeleton-cell" v-for="j in 5" :key="j"></div>
        </div>
      </div>
    </div>

    <transition name="fade" mode="out-in">
      <div v-if="!loading" class="content-container">
        <!--  -->
        <div class="page-header">
          <h1></h1>
          <div class="header-actions">
            <el-button
              v-if="selectedPresets && selectedPresets.length > 0"
              type="danger"
              :icon="Delete"
              @click="handleBatchDelete"
            >
               ({{ selectedPresets?.length || 0 }})
            </el-button>
            <el-button
              type="primary"
              :icon="Refresh"
              :loading="refreshing"
              @click="handleRefresh"
            >
              
            </el-button>
            <el-button
              type="primary"
              :icon="Plus"
              @click="handleCreate"
            >
              
            </el-button>
            <el-button
              type="success"
              :icon="Upload"
              @click="handleImport"
            >
              
            </el-button>
          </div>
        </div>

        <!--  -->
        <div class="stats-bar">
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ totalCount }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ presetsStore.countByCategory.common }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ presetsStore.countByCategory.recommended }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ presetsStore.countByCategory.hidden }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ presetsStore.countBySource['built-in'] }} </span>
          </div>
          <div class="stat-item">
            <span class="stat-label"></span>
            <span class="stat-value">{{ presetsStore.countBySource.custom }} </span>
          </div>
        </div>

        <!--  -->
        <div class="search-filter-bar">
          <div class="search-section">
            <el-input
              v-model="searchKeyword"
              placeholder="..."
              :prefix-icon="Search"
              clearable
              @input="handleSearch"
              style="width: 300px"
            />
          </div>
          <div class="filter-section">
            <el-select
              v-model="categoryFilter"
              placeholder=""
              clearable
              style="width: 120px"
              @change="handleFilterChange"
            >
              <el-option label="" value="common" />
              <el-option label="" value="recommended" />
              <el-option label="" value="hidden" />
            </el-select>
            <el-select
              v-model="sourceFilter"
              placeholder=""
              clearable
              style="width: 120px"
              @change="handleFilterChange"
            >
              <el-option label="" value="built-in" />
              <el-option label="" value="custom" />
            </el-select>
          </div>
        </div>

        <!--  -->
        <div class="presets-list">
          <div v-if="(!displayedPresets || displayedPresets.length === 0) && !loading" class="empty-state">
            <el-empty description="">
              <el-button type="primary" :icon="Plus" @click="handleCreate">
                
              </el-button>
            </el-empty>
          </div>

          <!--  -->
          <div v-else class="presets-waterfall" ref="waterfallRef">
            <preset-card
              v-for="preset in displayedPresets"
              :key="getPresetId(preset)"
              :preset="preset"
              :selected="isPresetSelected(preset)"
              @select="(selected) => handlePresetSelect(getPresetId(preset), selected)"
              @edit="handleEdit"
              @delete="handleDelete"
              @export="handleExport"
            />
          </div>

          <!--  -->
          <div v-if="shouldShowTrigger" ref="loadTrigger" class="load-trigger">
            <div v-if="loadingMore" class="loading-more">
              <el-skeleton :rows="2" animated />
            </div>
            <div v-else-if="loadError" class="load-error">
              <el-result icon="warning" title="" sub-title="">
                <template #extra>
                  <el-button type="primary" @click="retryLoad"></el-button>
                </template>
              </el-result>
            </div>
            <div v-else class="load-placeholder">
              <!--  Intersection Observer -->
            </div>
          </div>

          <!--  -->
          <div v-if="!hasMore && displayedPresets.length > 0" class="no-more">
            <el-divider>
              <span class="no-more-text"></span>
            </el-divider>
          </div>

          <!--  -->
          <div v-if="loading" class="loading-more">
            <el-skeleton :rows="3" animated />
          </div>
        </div>
      </div>
    </transition>

    <!--  -->
    <preset-editor
      :visible="editorVisible"
      :mode="editorMode"
      :preset="editorPreset"
      @close="handleEditorClose"
      @submit="handleEditorSubmit"
    />

    <!--  -->
    <input
      ref="fileInput"
      type="file"
      accept=".json"
      style="display: none"
      @change="handleFileSelect"
    />
  </div>
</template>

<script setup>
import PresetCard from '@/components/settings/PresetCard.vue';
import PresetEditor from '@/components/settings/PresetEditor.vue';
import { usePresetsStore } from '@/stores/presetsStore.js';
import {
  Delete,
  Plus,
  Refresh,
  Search,
  Upload
} from '@element-plus/icons-vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { computed, nextTick, onMounted, onUnmounted, ref, watch } from 'vue';

const presetsStore = usePresetsStore();

const loading = ref(true);
const refreshing = ref(false);
const searchKeyword = ref('');
const categoryFilter = ref('');
const sourceFilter = ref('');
const selectedPresets = ref([]);
const editorVisible = ref(false);
const editorMode = ref('create');
const editorPreset = ref(null);
const fileInput = ref(null);
const waterfallRef = ref(null);
const loadTrigger = ref(null);

// 
const currentPage = ref(1);
const pageSize = ref(50); // 
const hasMore = computed(() => presetsStore.pagination?.hasMore || false);

// 
const shouldShowTrigger = computed(() => {
  return hasMore.value || loadingMore.value;
});
const loadingMore = ref(false);
const loadError = ref(false);

// 
const allPresets = computed(() => presetsStore.allPresets || []);
const totalCount = computed(() => presetsStore.totalCount || 0);

//  store 
const displayedPresets = computed(() => {
  return allPresets.value || [];
});

// ID
const getPresetId = (preset) => {
  return preset.name; //  name 
};

// 
const isPresetSelected = (preset) => {
  const presetId = getPresetId(preset);
  return presetId && selectedPresets.value.includes(presetId);
};

// 
let searchTimeout = null;

// 
watch([searchKeyword], () => {
  // 
  if (searchTimeout) {
    clearTimeout(searchTimeout);
  }
  searchTimeout = setTimeout(() => {
    handleFilterChange();
  }, 500); // 500ms 
});

// 
watch([categoryFilter, sourceFilter], () => {
  handleFilterChange();
});

//  hasMore 
watch(hasMore, (newHasMore, oldHasMore) => {
  console.log('hasMore changed:', { newHasMore, oldHasMore });
  if (!newHasMore) {
    cleanupObserver();
  } else if (newHasMore && !oldHasMore) {
    // 
    nextTick(() => {
      setupInfiniteScroll();
    });
  }
});

// 
const handleFilterChange = async () => {
  currentPage.value = 1;
  cleanupObserver(); // 
  await fetchPresetsWithFilters();
  //  DOM 
  await nextTick();
  if (hasMore.value) {
    setupInfiniteScroll();
  }
};

// 
const fetchPresetsWithFilters = async () => {
  const params = {
    start: 0,
    nums: pageSize.value
  };
  
  if (searchKeyword.value) {
    params.keyword = searchKeyword.value;
  }
  
  if (categoryFilter.value) {
    params.category = categoryFilter.value;
  }
  
  if (sourceFilter.value) {
    params.source = sourceFilter.value;
  }
  
  try {
    await presetsStore.fetchPresets(params);
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const loadMore = async () => {
  console.log('loadMore called', {
    hasMore: hasMore.value,
    loadingMore: loadingMore.value,
    pagination: presetsStore.pagination
  });
  
  if (hasMore.value && !loadingMore.value) {
    loadingMore.value = true;
    loadError.value = false;
    
    const params = {
      start: presetsStore.pagination.start + presetsStore.pagination.nums,
      nums: pageSize.value
    };
    
    if (searchKeyword.value) {
      params.keyword = searchKeyword.value;
    }
    
    if (categoryFilter.value) {
      params.category = categoryFilter.value;
    }
    
    if (sourceFilter.value) {
      params.source = sourceFilter.value;
    }
    
    console.log('Loading more with params:', params);
    
    try {
      await presetsStore.fetchPresets(params, true); //  append 
      console.log('Load more successful');
    } catch (error) {
      console.error('Load more failed:', error);
      loadError.value = true;
      ElMessage.error('' + error.message);
    } finally {
      loadingMore.value = false;
    }
  } else {
    console.log('loadMore conditions not met', {
      hasMore: hasMore.value,
      loadingMore: loadingMore.value
    });
  }
};

// 
const retryLoad = () => {
  loadError.value = false;
  loadMore();
};

// 
const handlePresetSelect = (presetId, selected) => {
  if (selected) {
    if (!selectedPresets.value.includes(presetId)) {
      selectedPresets.value.push(presetId);
    }
  } else {
    const index = selectedPresets.value.indexOf(presetId);
    if (index > -1) {
      selectedPresets.value.splice(index, 1);
    }
  }
};

// 
const handleSearch = () => {
  //  watch  handleFilterChange
};

// 
const handleRefresh = async () => {
  refreshing.value = true;
  try {
    await presetsStore.fetchPresets();
    ElMessage.success('');
  } catch (error) {
    ElMessage.error('' + error.message);
  } finally {
    refreshing.value = false;
  }
};

// 
const handleCreate = () => {
  editorMode.value = 'create';
  editorPreset.value = null;
  editorVisible.value = true;
};

// 
const handleEdit = (preset) => {
  editorMode.value = 'edit';
  editorPreset.value = preset;
  editorVisible.value = true;
};

// 
const handleDelete = async (preset) => {
  try {
    await ElMessageBox.confirm(
      ` "${preset.name}" `,
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    );

    const presetId = preset.name;
    if (!presetId) {
      throw new Error('');
    }
    await presetsStore.deletePreset(presetId);
    ElMessage.success('');
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  }
};

// 
const handleBatchDelete = async () => {
  try {
    await ElMessageBox.confirm(
      ` ${selectedPresets.value.length} `,
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    );

    const results = await presetsStore.batchDeletePresets(selectedPresets.value);
    const successCount = results.filter(r => r.success).length;
    ElMessage.success(` ${successCount} `);
    selectedPresets.value = [];
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  }
};

// 
const handleExport = async (preset) => {
  try {
    const presetId = preset.name;
    if (!presetId) {
      throw new Error('');
    }
    await presetsStore.exportPreset(presetId);
    ElMessage.success('');
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const handleImport = () => {
  fileInput.value?.click();
};

// 
const handleFileSelect = async (event) => {
  const file = event.target.files[0];
  if (!file) return;

  try {
    await presetsStore.importPreset(file);
    ElMessage.success('');
    // 
    event.target.value = '';
  } catch (error) {
    ElMessage.error('' + error.message);
  }
};

// 
const handleEditorClose = () => {
  editorVisible.value = false;
};

// 
const handleEditorSubmit = async ({ data, mode }) => {
  try {
    if (mode === 'create') {
      await presetsStore.createPreset(data);
      ElMessage.success('');
      // 
      editorVisible.value = false;
    } else {
      // 
      const presetId = editorPreset.value.name;
      if (!presetId) {
        ElMessage.error('');
        return; // 
      }
      await presetsStore.updatePreset(presetId, data);
      ElMessage.success('');
      // 
      editorVisible.value = false;
    }
  } catch (error) {
    ElMessage.error(mode === 'create' ? '' : '' + error.message);
    // toast
  }
};

// Intersection Observer 
let observer = null;

// 
const setupInfiniteScroll = () => {
  console.log('setupInfiniteScroll called', {
    loadTrigger: loadTrigger.value,
    hasMore: hasMore.value,
    loadingMore: loadingMore.value
  });
  
  if (!loadTrigger.value) {
    console.log('loadTrigger element not found');
    return;
  }

  //  Intersection Observer
  observer = new IntersectionObserver(
    (entries) => {
      const entry = entries[0];
      console.log('Observer triggered', {
        isIntersecting: entry.isIntersecting,
        hasMore: hasMore.value,
        loadingMore: loadingMore.value
      });
      
      // 
      if (entry.isIntersecting && hasMore.value && !loadingMore.value) {
        console.log('Loading more data...');
        loadMore();
      }
    },
    {
      //  100px 
      rootMargin: '100px',
      threshold: 0.1
    }
  );

  observer.observe(loadTrigger.value);
  console.log('Observer set up successfully');
};

// 
const cleanupObserver = () => {
  if (observer) {
    observer.disconnect();
    observer = null;
  }
};

// 
onMounted(async () => {
  loading.value = true;
  try {
    // 
    await fetchPresetsWithFilters();
    await new Promise(resolve => setTimeout(resolve, 300));
  } catch (error) {
    ElMessage.error('' + error.message);
  } finally {
    loading.value = false;
    // 
    await nextTick(); //  DOM 
    setupInfiniteScroll();
  }
});

// 
onUnmounted(() => {
  cleanupObserver();
});
</script>

