import { client } from '@/lib/runtime.js'
import { randomString } from '@/utils/generate.js'
import { onMounted, onUnmounted, ref } from 'vue'

export function useLogViewer() {
  // 
  const logs = ref([])
  const isSubscribed = ref(false)
  const stats = ref(null)
  
  // 
  const socket = ref(client.socket)
  const isConnected = ref(client.isConnected)
  
  const pendingRequests = new Map()

  // 
  let logMessageHandler = null

  // 
  const handleLogMessage = (data) => {
    console.log(':', data)
    const { request_id, type } = data

    // ID
    if (request_id && pendingRequests.has(request_id)) {
      const resolver = pendingRequests.get(request_id)
      pendingRequests.delete(request_id)
      
      // 
      if (type === 'subscribe' && data.success) {
        isSubscribed.value = true
        console.log('')
      } else if (type === 'unsubscribe' && data.success) {
        isSubscribed.value = false
        console.log('')
      }
      
      resolver(data)
      return
    }

    // 
    switch (type) {
      case 'SUBSCRIBED':
        isSubscribed.value = true
        console.log(':', data.data)
        break
        
      case 'UNSUBSCRIBED':
        isSubscribed.value = false
        console.log('')
        break
        
      case 'stream':
        console.log(':', data.data)
        addLog(data.data)
        break
        
      case 'ERROR':
        console.error('API:', data.error)
        break
        
      default:
        console.log(':', type, data)
    }
  }

  // 
  const initLogListener = () => {
    if (!socket.value || !socket.value.socket) {
      console.log('Socket ...')
      return
    }

    // 
    cleanupLogListener()

    // 
    logMessageHandler = (message) => {
      try {
        const data = JSON.parse(message)
        if (data.protocol === 'logs') {
          console.log(':', data)
          handleLogMessage(data)
        }
      } catch (error) {
        console.error(':', error)
      }
    }

    // 
    socket.value.socket.on('message', logMessageHandler)
    console.log('')
  }

  // 
  const cleanupLogListener = () => {
    if (socket.value && socket.value.socket && logMessageHandler) {
      socket.value.socket.off('message', logMessageHandler)
      console.log('')
    }
    logMessageHandler = null
  }

  // 
  const sendRequest = (type, data) => {
    return new Promise((resolve, reject) => {
      console.log(':', type, data)
      console.log(':', isConnected.value)
      console.log(' socket:', socket.value)
      
      // 
      if (!socket.value || !isConnected.value) {
        console.warn('')
        
        // 
        setTimeout(() => {
          if (type === 'subscribe') {
            resolve({
              success: true,
              data: { message: '' }
            })
          } else if (type === 'unsubscribe') {
            resolve({
              success: true,
              data: { message: '' }
            })
          } else {
            resolve({
              success: true,
              data: { message: ` ${type} ` }
            })
          }
        }, 500) // 
        return
      }

      const requestId = randomString(16)
      pendingRequests.set(requestId, resolve)

      // 
      setTimeout(() => {
        if (pendingRequests.has(requestId)) {
          pendingRequests.delete(requestId)
          const error = new Error('')
          console.error(':', requestId, type)
          reject(error)
        }
      }, 30000)

      const message = {
        request_id: requestId,
        protocol: 'logs',
        type,
        data
      }

      console.log(':', message)
      socket.value.sendMessage(message)
    })
  }

  // 
  const addLog = (log) => {
    logs.value.push(log)
    
    // 
    if (logs.value.length > 2000) {
      logs.value.splice(0, logs.value.length - 2000)
    }
  }

  // 
  const subscribe = async (options = {}) => {
    const defaultOptions = {
      level: 'INFO',
      modules: [],
      realtime: true,
      bufferSize: 1000,
      sendHistory: true
    }

    try {
      const response = await sendRequest('subscribe', { ...defaultOptions, ...options })
      
      // 
      if (response.success && (!socket.value || !isConnected.value)) {
        isSubscribed.value = true
        startMockLogStream()
      }
      //  handleLogMessage 
      
      return response
    } catch (error) {
      console.error(':', error)
      throw error
    }
  }

  // 
  let mockLogInterval = null
  
  const startMockLogStream = () => {
    console.log('')
    let counter = 1
    
    mockLogInterval = setInterval(() => {
      const mockLog = {
        id: `mock_stream_${counter}`,
        timestamp: new Date().toISOString(),
        level: ['INFO', 'WARN', 'ERROR', 'DEBUG'][Math.floor(Math.random() * 4)],
        module: ['llm', 'onebot', 'system'][Math.floor(Math.random() * 3)],
        message: ` #${counter}`,
        caller: `mock.js:${Math.floor(Math.random() * 100)}`,
        ip: '127.0.0.1'
      }
      
      addLog(mockLog)
      counter++
    }, 2000) // 2
  }
  
  const stopMockLogStream = () => {
    if (mockLogInterval) {
      clearInterval(mockLogInterval)
      mockLogInterval = null
      console.log('')
    }
  }

  // 
  const unsubscribe = async () => {
    try {
      const response = await sendRequest('unsubscribe', {})
      
      // 
      if (response.success && (!socket.value || !isConnected.value)) {
        isSubscribed.value = false
        stopMockLogStream()
      }
      //  handleLogMessage 
      
      return response
    } catch (error) {
      console.error(':', error)
      throw error
    }
  }

  // 
  const search = async (query) => {
    const response = await sendRequest('search', query)
    return response
  }

  // 
  const exportLogs = async (options) => {
    const response = await sendRequest('export', options)
    return response
  }



  // 
  const updateConfig = async (type, value) => {
    const response = await sendRequest('config', { type, value })
    return response
  }

  // 
  const getStats = async () => {
    try {
      const response = await sendRequest('stats', {})
      if (response.success) {
        stats.value = response.data
      }
      return response
    } catch (error) {
      console.error(':', error)
      return { success: false, error }
    }
  }

  // 
  const clearLogs = () => {
    logs.value = []
  }



  //  client socket 
  const setupSocketListeners = () => {
    if (!client.socket) {
      console.log('Client socket ')
      return null
    }
    
    // 
    const onConnect = (info) => {
      console.log('Socket :', info)
      isConnected.value = true
      socket.value = client.socket
      setTimeout(() => {
        initLogListener()
      }, 100)
    }
    
    // 
    const onConnectError = (error) => {
      console.log('Socket :', error)
      isConnected.value = false
      cleanupLogListener()
    }
    
    //  client.socket
    client.socket.on('connect', onConnect)
    client.socket.on('connect_error', onConnectError)
    
    // 
    return () => {
      if (client.socket) {
        client.socket.off('connect', onConnect)
        client.socket.off('connect_error', onConnectError)
      }
    }
  }

  // 
  const setupStatusPolling = () => {
    let lastSocketId = socket.value ? socket.value.id : null
    
    const checkStatus = () => {
      const currentConnected = client.isConnected
      const currentSocket = client.socket
      const currentSocketId = currentSocket ? currentSocket.id : null
      
      // 
      if (currentConnected !== isConnected.value) {
        console.log(':', isConnected.value, '->', currentConnected)
        isConnected.value = currentConnected
        
        if (currentConnected && currentSocket) {
          socket.value = currentSocket
          lastSocketId = currentSocketId
          initLogListener()
        } else {
          socket.value = null
          lastSocketId = null
          cleanupLogListener()
        }
      }
      
      //  socket  ID 
      if (currentSocketId !== lastSocketId) {
        console.log(' socket :', lastSocketId, '->', currentSocketId)
        socket.value = currentSocket
        lastSocketId = currentSocketId
        
        if (currentConnected && currentSocket) {
          initLogListener()
        } else {
          cleanupLogListener()
        }
      }
    }
    
    // 2
    const interval = setInterval(checkStatus, 2000)
    
    return () => {
      clearInterval(interval)
    }
  }

  // 
  const addTestLogs = () => {
    const testLogs = [
      {
        id: 'test_1',
        timestamp: new Date(Date.now() - 60000).toISOString(),
        level: 'INFO',
        module: 'system',
        message: '',
        caller: 'main.js:1',
        ip: '127.0.0.1'
      },
      {
        id: 'test_2',
        timestamp: new Date(Date.now() - 45000).toISOString(),
        level: 'INFO',
        module: 'llm',
        message: 'OpenAI API ',
        caller: 'openai.js:197',
        ip: '127.0.0.1',
        extra: {
          type: 'json',
          source: 'openai_api',
          originalObject: {
            request: {
              model: 'gpt-4',
              messages: [{ role: 'user', content: 'Hello world' }],
              temperature: 0.7,
              max_tokens: 1000
            },
            response: {
              id: 'chatcmpl-123',
              choices: [{ message: { role: 'assistant', content: 'Hello! How can I help you today?' } }],
              usage: { prompt_tokens: 10, completion_tokens: 15, total_tokens: 25 }
            }
          }
        }
      },
      {
        id: 'test_3',
        timestamp: new Date(Date.now() - 30000).toISOString(),
        level: 'WARN',
        module: 'llm',
        message: 'API ',
        caller: 'openai.js:123',
        ip: '127.0.0.1'
      },
      {
        id: 'test_4',
        timestamp: new Date(Date.now() - 15000).toISOString(),
        level: 'ERROR',
        module: 'onebot',
        message: 'OneBot ',
        caller: 'onebot.js:45',
        ip: '127.0.0.1',
        extra: {
          type: 'json',
          error_code: 'CONNECTION_FAILED',
          originalObject: {
            error: {
              code: 'CONNECTION_FAILED',
              message: ' OneBot ',
              details: {
                host: 'localhost',
                port: 5700,
                timeout: 5000,
                retries: 3
              }
            }
          }
        }
      },
      {
        id: 'test_5',
        timestamp: new Date().toISOString(),
        level: 'ERROR',
        module: 'onebot',
        message: '...',
        caller: 'onebot.js:45',
        ip: '127.0.0.1'
      }
    ]
    
    logs.value.push(...testLogs)
    console.log(':', testLogs.length, '')
  }

  // 
  let cleanupSocketListeners = null
  let cleanupStatusPolling = null

  // 
  const autoSubscribe = async () => {
    try {
      console.log('...')
      await subscribe({
        level: 'INFO',
        modules: [],
        realtime: true,
        bufferSize: 1000,
        sendHistory: true
      })
      console.log('')
    } catch (error) {
      console.error(':', error)
      // 
      if (!socket.value || !isConnected.value) {
        console.log('')
        isSubscribed.value = true
        startMockLogStream()
      }
    }
  }

  // 
  onMounted(() => {
    console.log('LogViewer ')
    console.log('Client :', client.isConnected)
    console.log('Client  socket:', client.socket)
    
    // 
    isConnected.value = client.isConnected
    socket.value = client.socket
    
    //  socket 
    cleanupSocketListeners = setupSocketListeners()
    
    // 
    cleanupStatusPolling = setupStatusPolling()
    
    // 
    if (isConnected.value && socket.value) {
      initLogListener()
    }
    
    // 
    setTimeout(() => {
      addTestLogs()
    }, 1000)
    
    // 
    setTimeout(() => {
      autoSubscribe()
    }, 2000)
  })

  // 
  onUnmounted(() => {
    // 
    cleanupLogListener()
    
    // 
    stopMockLogStream()
    
    //  socket 
    if (cleanupSocketListeners) {
      cleanupSocketListeners()
    }
    
    // 
    if (cleanupStatusPolling) {
      cleanupStatusPolling()
    }
    
    // 
    pendingRequests.clear()
  })

  return {
    // 
    logs,
    isConnected,
    stats,
    
    // 
    search,
    exportLogs,
    updateConfig,
    getStats,
    clearLogs
  }
}