<template>
  <div class="logs-view">
    <div class="logs-header">
      <h2></h2>
      <p class="logs-description"></p>
    </div>

    <!--  -->
    <el-card class="connection-card" shadow="never">
      <div class="connection-status">
        <div class="status-indicator" :class="{ 'connected': isConnected, 'disconnected': !isConnected }">
          <div class="status-dot"></div>
          <span>{{ isConnected ? '' : '' }}</span>
        </div>
        <div class="connection-info">
          <span v-if="isConnected && stats">
            : {{ formatUptime(stats.service?.uptime) }} | 
            : {{ stats.service?.totalLogsSent || 0 }}
          </span>
          <span v-else-if="isConnected">
            ...
          </span>
          <span v-else>
            
          </span>
        </div>
      </div>
      
      <!--  -->
      <div class="debug-info" style="margin-top: 12px; font-size: 12px; color: #909399;">
        <div>: {{ isConnected ? ' ' : ' ' }}</div>
        <div>: {{ logs.length }} </div>
      </div>
    </el-card>

    <!--  -->
    <el-card class="control-panel" shadow="never">
      <div class="control-row">
        <div class="control-group">
          <el-button @click="clearLogs" :disabled="logs.length === 0">
            
          </el-button>
          <el-button @click="exportLogs" :disabled="logs.length === 0">
            
          </el-button>
        </div>
        
        <div class="filter-group">
          <el-select v-model="filterLevel" placeholder="" style="width: 120px">
            <el-option label="" value=""></el-option>
            <el-option label="ERROR" value="ERROR"></el-option>
            <el-option label="WARN" value="WARN"></el-option>
            <el-option label="MARK" value="MARK"></el-option>
            <el-option label="INFO" value="INFO"></el-option>
            <el-option label="DEBUG" value="DEBUG"></el-option>
          </el-select>
          
          <el-select v-model="filterModule" placeholder="" style="width: 120px">
            <el-option label="" value=""></el-option>
            <el-option label="llm" value="llm"></el-option>
            <el-option label="onebot" value="onebot"></el-option>
            <el-option label="system" value="system"></el-option>
          </el-select>
          
          <el-input
            v-model="searchKeyword"
            placeholder=""
            style="width: 200px"
            clearable
            @keyup.enter="performSearch"
          >
            <template #append>
              <el-button @click="performSearch" :icon="Search"></el-button>
            </template>
          </el-input>
        </div>
      </div>
    </el-card>

    <!--  -->
    <el-card class="logs-container" shadow="never">
      <div class="logs-header-row">
        <span class="logs-count"> {{ filteredLogs.length }} </span>
        <div class="logs-controls">
          <el-switch
            v-model="autoScroll"
            active-text=""
            inactive-text=""
          />
          <el-button 
            size="small" 
            @click="scrollToBottom"
            :disabled="filteredLogs.length === 0"
          >
            
          </el-button>
        </div>
      </div>
      
      <div 
        ref="logsContainer" 
        class="logs-list"
        @scroll="handleScroll"
      >
        <div 
          v-for="log in filteredLogs" 
          :key="log.id"
          class="log-item"
          :class="[`level-${log.level.toLowerCase()}`, { 'highlighted': log.highlighted }]"
        >
          <div class="log-header">
            <span class="log-timestamp">{{ formatTimestamp(log.timestamp) }}</span>
            <span class="log-level" :class="`level-${log.level.toLowerCase()}`">{{ log.level }}</span>
            <span class="log-module">{{ log.module }}</span>
            <span v-if="log.caller" class="log-caller">{{ log.caller }}</span>
          </div>
          <div class="log-message">
            <span v-if="log.extra?.type === 'json'" class="json-message" @click="openJsonDialog(log)">
              {{ getJsonSummary(log) }}
              <el-icon class="json-expand-icon"><View /></el-icon>
            </span>
            <span v-else v-html="log.message"></span>
          </div>
          <div v-if="log.extra && Object.keys(getDisplayExtra(log.extra)).length > 0" class="log-extra">
            <el-tag 
              v-for="(value, key) in getDisplayExtra(log.extra)" 
              :key="key"
              size="small"
              type="info"
              class="log-extra-tag"
              :title="`${key}: ${value}`"
            >
              {{ key }}: {{ truncateText(value, 20) }}
            </el-tag>
          </div>
        </div>
        
        <div v-if="filteredLogs.length === 0" class="empty-logs">
          <el-empty description="" />
        </div>
      </div>
    </el-card>

    <!-- JSON  -->
    <el-dialog v-model="jsonDialogVisible" title="JSON " width="800px" class="json-dialog">
      <div class="json-content">
        <div class="json-header">
          <span class="json-timestamp">{{ selectedLog?.timestamp }}</span>
          <span class="json-level" :class="`level-${selectedLog?.level?.toLowerCase()}`">
            {{ selectedLog?.level }}
          </span>
          <span class="json-module">{{ selectedLog?.module }}</span>
        </div>
        <div class="json-body">
          <pre class="json-pre">{{ formatJsonContent(selectedLog) }}</pre>
        </div>
      </div>
      <template #footer>
        <el-button @click="copyJsonContent"></el-button>
        <el-button type="primary" @click="jsonDialogVisible = false"></el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="exportDialogVisible" title="" width="500px">
      <el-form :model="exportForm" label-width="100px">
        <el-form-item label="">
          <el-radio-group v-model="exportForm.format">
            <el-radio label="json">JSON</el-radio>
            <el-radio label="csv">CSV</el-radio>
            <el-radio label="txt">TXT</el-radio>
          </el-radio-group>
        </el-form-item>
        
        <el-form-item label="">
          <el-input-number 
            v-model="exportForm.maxRecords" 
            :min="1" 
            :max="50000"
            style="width: 100%"
          />
        </el-form-item>
        
        <el-form-item label="">
          <el-switch v-model="exportForm.includeMetadata" />
        </el-form-item>
      </el-form>
      
      <template #footer>
        <el-button @click="exportDialogVisible = false"></el-button>
        <el-button type="primary" @click="confirmExport" :loading="exportLoading">
          
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { useLogViewer } from '@/composables/useLogViewer'
import { Search, View } from '@element-plus/icons-vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { computed, nextTick, onMounted, onUnmounted, ref, watch } from 'vue'

//  composable
const {
  logs,
  isConnected,
  stats,
  search,
  exportLogs: exportLogsData,
  getStats,
  clearLogs: clearLogsData
} = useLogViewer()

// 
const filterLevel = ref('')
const filterModule = ref('')
const searchKeyword = ref('')

// UI 
const autoScroll = ref(true)
const logsContainer = ref(null)
const exportDialogVisible = ref(false)
const exportLoading = ref(false)
const jsonDialogVisible = ref(false)
const selectedLog = ref(null)

// 
const exportForm = ref({
  format: 'json',
  maxRecords: 10000,
  includeMetadata: true
})

// 
const filteredLogs = computed(() => {
  let filtered = logs.value

  if (filterLevel.value) {
    filtered = filtered.filter(log => log.level === filterLevel.value)
  }

  if (filterModule.value) {
    filtered = filtered.filter(log => log.module === filterModule.value)
  }

  if (searchKeyword.value) {
    const keyword = searchKeyword.value.toLowerCase()
    filtered = filtered.filter(log => 
      log.message.toLowerCase().includes(keyword) ||
      log.module.toLowerCase().includes(keyword) ||
      (log.caller && log.caller.toLowerCase().includes(keyword))
    )
  }

  return filtered
})



// 
const clearLogs = async () => {
  try {
    await ElMessageBox.confirm('', '', {
      type: 'warning'
    })
    clearLogsData()
    ElMessage.success('')
  } catch (error) {
    // 
  }
}

// 
const performSearch = async () => {
  if (!searchKeyword.value.trim()) {
    return
  }

  try {
    const results = await search({
      keyword: searchKeyword.value,
      level: filterLevel.value || undefined,
      modules: filterModule.value ? [filterModule.value] : [],
      page: 1,
      pageSize: 100
    })
    
    if (results.success) {
      ElMessage.success(` ${results.data.total} `)
    }
  } catch (error) {
    ElMessage.error(': ' + error.message)
  }
}

// 
const exportLogs = () => {
  exportDialogVisible.value = true
}

// 
const confirmExport = async () => {
  exportLoading.value = true
  
  try {
    const result = await exportLogsData({
      format: exportForm.value.format,
      level: filterLevel.value || undefined,
      modules: filterModule.value ? [filterModule.value] : [],
      keyword: searchKeyword.value || undefined,
      maxRecords: exportForm.value.maxRecords,
      includeMetadata: exportForm.value.includeMetadata
    })
    
    if (result.success) {
      // 
      const blob = new Blob([result.data.data], {
        type: getContentType(result.data.format)
      })
      
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = `logs_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.${result.data.format}`
      a.click()
      
      URL.revokeObjectURL(url)
      ElMessage.success('')
      exportDialogVisible.value = false
    }
  } catch (error) {
    ElMessage.error(': ' + error.message)
  } finally {
    exportLoading.value = false
  }
}

// 
const getContentType = (format) => {
  const types = {
    json: 'application/json',
    csv: 'text/csv',
    txt: 'text/plain'
  }
  return types[format] || 'text/plain'
}

// 
const formatTimestamp = (timestamp) => {
  return new Date(timestamp).toLocaleString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    fractionalSecondDigits: 3
  })
}

// 
const formatUptime = (uptime) => {
  if (!uptime) return ''
  
  const seconds = Math.floor(uptime / 1000)
  const minutes = Math.floor(seconds / 60)
  const hours = Math.floor(minutes / 60)
  const days = Math.floor(hours / 24)
  
  if (days > 0) {
    return `${days}${hours % 24}`
  } else if (hours > 0) {
    return `${hours}${minutes % 60}`
  } else if (minutes > 0) {
    return `${minutes}`
  } else {
    return `${seconds}`
  }
}

// 
const truncateText = (text, maxLength) => {
  if (!text) return ''
  const str = String(text)
  if (str.length <= maxLength) return str
  return str.substring(0, maxLength) + '...'
}

//  JSON 
const getJsonSummary = (log) => {
  try {
    const jsonObj = log.extra?.originalObject
    if (!jsonObj || typeof jsonObj !== 'object') {
      return 'JSON: '
    }
    
    const keys = Object.keys(jsonObj)
    if (keys.length === 0) {
      return 'JSON: {}'
    } else if (keys.length === 1) {
      return `JSON: { ${keys[0]}: ... }`
    } else {
      return `JSON: { ${keys[0]}, ${keys[1]}${keys.length > 2 ? `, +${keys.length - 2} more` : ''} }`
    }
  } catch (error) {
    return 'JSON: '
  }
}

//  JSON 
const formatJsonContent = (log) => {
  try {
    const jsonObj = log.extra?.originalObject
    if (!jsonObj) {
      return ' JSON '
    }
    return JSON.stringify(jsonObj, null, 2)
  } catch (error) {
    return ''
  }
}

//  JSON 
const openJsonDialog = (log) => {
  selectedLog.value = log
  jsonDialogVisible.value = true
}

//  JSON 
const copyJsonContent = async () => {
  if (!selectedLog.value) return
  
  try {
    const formattedJson = formatJsonContent(selectedLog.value)
    await navigator.clipboard.writeText(formattedJson)
    ElMessage.success('JSON ')
  } catch (error) {
    ElMessage.error(': ' + error.message)
  }
}

//  extra 
const getDisplayExtra = (extra) => {
  if (!extra) return {}
  
  const { type, originalObject, ...displayExtra } = extra
  return displayExtra
}

// 
const scrollToBottom = () => {
  if (logsContainer.value) {
    nextTick(() => {
      logsContainer.value.scrollTop = logsContainer.value.scrollHeight
    })
  }
}

// 
const handleScroll = () => {
  if (!logsContainer.value || !autoScroll.value) return
  
  const { scrollTop, scrollHeight, clientHeight } = logsContainer.value
  const isAtBottom = scrollTop + clientHeight >= scrollHeight - 10
  
  if (!isAtBottom) {
    autoScroll.value = false
  }
}

// 
watch(filteredLogs, () => {
  if (autoScroll.value) {
    nextTick(() => {
      scrollToBottom()
    })
  }
}, { deep: true })

// 
let statsInterval = null

onMounted(() => {
  // 30
  statsInterval = setInterval(() => {
    if (isConnected.value) {
      getStats()
    }
  }, 30000)
  
  // 
  if (isConnected.value) {
    getStats()
  }
})

onUnmounted(() => {
  if (statsInterval) {
    clearInterval(statsInterval)
  }
})
</script>

